html{
    min-height: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background: url(/image/mario-bg.png) no-repeat;
    background-size:100% ;
    background-position: bottom;
    background-color: #95e8f3;
    min-height: 100%;
}
form {
    padding: 20px;
    margin-top: 60px
}
.brand-name {
    font-size: 30px;
}
.card .card-title {
    font-weight: 400;
}
form button, form h5 {
    margin: 20px 0
}
input[type=text]:not(.browser-default):focus:not([readonly]),
input[type=email]:not(.browser-default):focus:not([readonly]),
input[type=password]:not(.browser-default):focus:not([readonly]),
textarea.materilize-textarea:focus:not([readonly]){
    border-color: #ec407a;
    box-shadow: none
}
